'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace constants
*/
var constants = {};

/**
* @name MAX
* @memberof constants
* @readonly
* @constant
* @type {number}
* @see {@link module:@stdlib/constants/int16/max}
*/
import MAX from '@stdlib/constants-int16-max';
setReadOnly( constants, 'MAX', MAX );

/**
* @name MIN
* @memberof constants
* @readonly
* @constant
* @type {number}
* @see {@link module:@stdlib/constants/int16/min}
*/
import MIN from '@stdlib/constants-int16-min';
setReadOnly( constants, 'MIN', MIN );

/**
* @name NUM_BYTES
* @memberof constants
* @readonly
* @constant
* @type {number}
* @see {@link module:@stdlib/constants/int16/num-bytes}
*/
import NUM_BYTES from '@stdlib/constants-int16-num-bytes';
setReadOnly( constants, 'NUM_BYTES', NUM_BYTES );


// EXPORTS //

export default constants;
